/**
  * ===========================================================
  * PAGE HEADER desktop
  * ===========================================================
*/

.card__tabs {
  margin-bottom: 100px;
}

.card__tab-box {
  background: #F0F0F4;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 22px 0;
  margin-bottom: 50px;
}

.card__tab {
  font-size: 20px;
  line-height: 24px;

  color: #2F3035;
  opacity: 0.6;
}

.card__tab.tab--active {
  opacity: 1;
  border-bottom: 2px solid #1C62CD;
}

.card__top-line {
  margin-bottom: 10px;
}

.card__label-search {
  font-size: 17px;
  line-height: 20px;
  color: rgba(47, 48, 53, 0.7);
}

.card__input-search {
  margin-left: 17px;
  background: #F0F0F4;
  border-radius: 3px;
  padding: 9px 37px 9px 17px;
  border: none;
  margin-right: 100px;
}

.card__label-pickup {
  margin-left: 54px;
  font-size: 17px;
  line-height: 20px;
  color: rgba(47, 48, 53, 0.7);
}

.card__list-item {
  padding: 20px 0;
  border-bottom: 1px solid rgba(47, 48, 53, 0.1);
  display: flex;
  font-size: 16px;
  line-height: 22px;
  color: rgba(47, 48, 53, 0.7);
}

.card__list-itemtitle {
  font-size: 20px;
  line-height: 24px;
  color: #2F3035;
}

.card__list-address {
  width: 220px;
  margin-right: 80px;
}

.card__list-workhours {
  width: 157px;
  margin-right: 96px;
}

.workhours {
  display: flex;
  justify-content: space-between;
}

.card__list-available {
  width: 140px;
  margin-right: 60px;
}

.card__list-num {
  width: 104px;
}

.card__list-btn {
  margin-left: auto;
}

.card__list-btn button {
  font-size: 13px;
  line-height: 16px;
  letter-spacing: 0.12em;
  color: #FFFFFF;
  padding: 9px 40px;
  background: #1C62CD;
  border-radius: 3px;
  text-transform: uppercase;
  border: none;
  cursor: pointer;
}



/**
  * ===========================================================
  * PAGE HEADER tablet
  * ===========================================================
*/

@media (max-width: @tablet-width) {}



