/**
  * ===========================================================
  * PAGE HEADER desktop
  * ===========================================================
*/
.header__top-inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 122px;
  font-family: 'SFProDisplay-Bold', sans-serif;
  font-weight: 700;
}

.menu__item {
  display: inline-block;
}

.menu__item+.menu__item {
  padding-left: 50px;
}

.header__box {
  display: flex;
}

.menu__btn {
  display: none;
}

.header__address {
  margin-right: 60px;
  width: 265px;
  padding-left: 30px;
  position: relative;
}

.header__address::before {
  content: "";
  position: absolute;
  background-image: url('../../img/address-icon.svg');
  width: 30px;
  height: 30px;
  left: 0;
  top: -10px;
}

.user-list {
  margin-right: 15px;
  display: flex;
  align-items: center;
}

.menu-mobile__list {
  width: 243px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.15);
  transform: translateX(-100%);
  transition: all .3s;
  position: absolute;
  background-color: #fff;
}

.menu__mobile-linewrapper {
  display: none;
}

.menu-mobile__list--active {
  transform: translateX(0%);
}

.menu-mobile__item {
  position: relative;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.menu-mobile__link {
  display: block;
  padding-left: 53px;
  padding-top: 36px;
}

.menu-mobile__img {
  position: absolute;
  bottom: 0;
  left: 11px;
}

.user-list__item {
  padding-right: 10px;
}

.basket {
  position: relative;
}

.basket__num {
  position: absolute;
  font-family: 'SFProDisplay-Medium', sans-serif;
  font-weight: 500;
  font-size: 10px;
  line-height: 20px;
  width: 20px;
  height: 20px;
  color: #fff;
  background-color: #1C62CD;
  border-radius: 50%;
  text-align: center;
  bottom: -2px;
  right: -13px;
}

.header__bottom {
  margin-bottom: 80px;
}

.menu-categories {
  background-color: #F0F0F4;
  display: flex;
  justify-content: space-around;
  min-height: 53px;
  align-items: center;
}

.menu-categories__link {
  transition: all .3s;
  padding: 15px 0 14px;
  position: relative;
}

.menu-categories__link::before {
  content: "";
  position: absolute;
  width: 0;
  left: auto;
  bottom: -2px;
  right: 0;
  height: 3px;
  background-color: #1C62CD;
  transition: all .3s;
}

.menu-categories__link:hover {
  font-family: 'SFProDisplay-Semibold', sans-serif;
  color: #000;
}

.menu-categories__link:hover::before {
  width: 100%;
  right: 0;
  left: 0;
  right: auto;
}

/**
  * ===========================================================
  * PAGE HEADER tablet
  * ===========================================================
*/

@media (max-width: @tablet-width) {}


